export default {
	data() {
		return {
			totalPrice: 0,
			allChecked: uni.getStorageSync('allCh') || true,
			lists:[]
		}
	},
	onLoad() {
		this.lists=uni.getStorageSync("cart")||[];
		this.totalPrice=this._totalPrice()
	},
	methods: {
		//计算总价
		_totalPrice(index){
		    let list = this.lists;
		    let allPrice = 0
		    list.forEach(item => {
		      if(item.status){
		        allPrice += item.count * item.price
		      }
		    })
		    return allPrice
		},
		goHome(){
			uni.switchTab({
				url:"/pages/home/home"
			})
		},
		//加按钮
		jia(index){
			let count=this.lists[index].count
			count++
			this.lists[index].count=count
			uni.setStorageSync('cart', this.lists)
		},
		//减按钮
		jian(index){
			console.log(index)
			let count=this.lists[index].count
			let price=this.lists[index].price
			if(count<=1){
				count=1
			}else{
				count--
				
			}
			
			this.lists[index].count=count
			 uni.setStorageSync('cart', this.lists)
		},
		//单选
		danxuan(index){
			this.lists[index].status=!this.lists[index].status
			for (var i = 0; i < this.lists.length ; i++) {
			  if (!this.lists[i].status) {
				this.allChecked = false;
				break;
			  }
			}

			this.list=this.lists,
			this.allChecked=this.allChecked
			
			wx.setStorageSync('cart', this.lists)
			wx.setStorageSync('allCh', this.allChecked)
			this.totalPrice=this._totalPrice()
		},
		//全选
		allCheck(index){
			let allStatus = wx.getStorageSync('allCh')
			let list = this.lists
			allStatus = !allStatus
			list.forEach(item => {
			  item.status = allStatus
			})
			  this.allChecked = allStatus,
			  this.lists=list
			wx.setStorageSync('allCh', allStatus)
			wx.setStorageSync('cart', this.lists)
			this.totalPrice=this._totalPrice()
		},
		//删除
		remove(index){
			uni.showModal({
			  title: '提示',
			  content: "确定删除吗？",
			  success: res => {
				if (res.confirm) {
				  console.log(res.confirm)
				  wx.showToast({
					title: '删除成功',
					icon: 'success',
					duration: 2000
				  })
				  //删除逻辑
				  let list = this.lists;
				  
				  list.splice(index, 1);
				  this.totalPrice=this._totalPrice()
				  this.lists=list

				  wx.setStorageSync('cart',this.lists)
				} else if (res.cancel) {
				  console.log("取消")
				}
			  }
			})
		}
	},
	
}
